	module.exports = {"data":{"markdownRemark":{"headings":[{"value":"Project Overview","depth":2},{"value":"Target Audience","depth":2},{"value":"Key Features","depth":2},{"value":"Why Spots Matters","depth":2}],"html":"<h2>Project Overview</h2>\n<p>If you are in a big city, finding a new place to eat can be overwhelming. Researching different options can take forever, and it’s always hard to know if reviews are trustworthy. SPOTS makes it quick, fun, and easy to discover local places to eat, drink, and hang out. The app connects you to restaurants, bars, and cafes that are close to your current location, and it provides you with curated recommendations based on your existing habits and preferences.</p>\n<h2>Target Audience</h2>\n<p>SPOTS is designed for people are who looking to try new places in the moment, either in their home city or abroad. </p>\n<h2>Key Features</h2>\n<p>SPOTS enables individuals to do three key things: discover new places, compare their options effortlessly, and fine tune their Spots to suit their tastes.</p>\n<p>![The three key features of SPOTS](../assets/spots features.png)</p>\n<p><em><strong>Discover New Places</strong></em></p>\n<p>SPOTS shows the user a variety of nearby local Spots based on their similarity to places they like. The user can either discard the recommendations, or like them.</p>\n<p>![App screen showing how the user discovers new Spots](../assets/spots features-05.png)</p>\n<p><strong><em>Compare Options Effortlessly</em></strong></p>\n<p>The Spots that the user likes are stored in a place where they can be filtered, sorted, searched, and rated. </p>\n<p>![Screen showing where liked Spots are stored](../assets/spots features-06.png)</p>\n<p><em><strong>Fine Tune Spots to Suit Your Tastes</strong></em></p>\n<p>In the detailed Spot view, users can rate Spots once they’ve visited them. The user-provided ratings feed into and help to refine the app’s future recommendations.</p>\n<p>![Screen showing how user rates Spots](../assets/spots features-07.png)</p>\n<h2>Why Spots Matters</h2>\n<p>Although there are plenty of apps and websites that rate and recommend different food and drink venues, they can take lots of time to navigate and understand. SPOTS makes finding new places easy by showing users one nearby option at a time, and using their own preferences and social connections to support the recommendations.</p>","frontmatter":{"title":"Spots","graduate":"Danielle Sheahan","category":"Product Design","category2":"UI/UX","category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/the-next-website/static/spots main-2fea76c2366cf5bb2a08633556c723c7-a7a62.jpg","srcSet":"/the-next-website/static/spots main-2fea76c2366cf5bb2a08633556c723c7-5a1a9.jpg 375w,\n/the-next-website/static/spots main-2fea76c2366cf5bb2a08633556c723c7-0908c.jpg 750w,\n/the-next-website/static/spots main-2fea76c2366cf5bb2a08633556c723c7-a7a62.jpg 1500w,\n/the-next-website/static/spots main-2fea76c2366cf5bb2a08633556c723c7-83d7d.jpg 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/spots/","name":"/Spots/"}}

